@import "../../scss/variable";
.skill {
     &__container {
          position: fixed;
          right: 20px;
          top: 0;
          width: 35%;
          height: 100vh;
          display: flex;
          padding-top: 60px;
          flex-direction: row-reverse;
     }
     &__avatar {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          padding-top: 160px;
          width: 100%;
          height: 100vh;
          flex-direction: column;
          border-left: 1px solid $TextClr2;
          gap: 30px;
          & .skill__button {
               transition: 0.35s ease;
               &.active1 {
                    background-color: $mainClr;
                    color: white;
               }
               cursor: pointer;
               font-weight: 400;
               background-color: white;
               padding: 2px 10px;
               color: $TextClr2;
               box-shadow: 0 0 2px $TextClr2;
               line-height: 1.4;
               border-radius: 10px;
               font-size: 16px;
               &:hover {
                    opacity: 0.95;
               }
          }
     }
}
